
@charset 'utf-8';
.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-ico-ellipsis, .tui-ico-first, .tui-ico-last, .tui-ico-next, .tui-ico-prev {
    overflow: hidden;
}

.tui-pagination {
    height: 44px;
    margin: 0 0 40px;
    line-height: normal;
    text-align: center;
    font-size: 0;
        }


.tui-pagination .tui-page-btn {
    display: inline-block;
    position: relative;
    width: 33px;
    height: 33px;
    padding: 8px 0;
    margin-right: 8px;
    color: #333;
    font-size: 18px;
    font-weight: bold;
    line-height: 1;
    text-decoration: none;
    vertical-align: middle;
    border: 1px solid #111;
   //  border-width: 1px 0;
    border-radius: 50%;
}

.tui-pagination .tui-first{
    margin-right: 5px;
}


.tui-pagination .tui-page-btn:hover {
    color:  #EAC645;
    // background-color: #EAC645;
}

.tui-pagination .tui-is-selected, .tui-pagination strong {
    color: #fff;
    background: #111;
    border-color: #111;
    cursor: default;
}

.tui-pagination .tui-is-selected:hover {
    background-color: #111;
}

.tui-pagination .tui-first-child.tui-is-selected, .tui-pagination strong.tui-first-child {
   //  margin-left: 24px;
}

.tui-pagination .tui-first-child, .tui-pagination .tui-prev-is-ellip {
   //  border-left: 1px solid #ddd
}

.tui-pagination .tui-last-child, .tui-pagination .tui-next-is-ellip {
   //  border-right: 1px solid #ddd
}

.tui-pagination .tui-first-child.tui-is-selected {
   //  border-left: 1px solid #4b96e6
}

.tui-pagination .tui-last-child.tui-is-selected {
   //  border-right: 1px solid #4b96e6
}

.tui-pagination .tui-first, .tui-pagination .tui-prev, .tui-pagination .tui-next, .tui-pagination .tui-last, .tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    height: 33px;
    padding: 0;
    font-size: 0;
    line-height: 21px;
    white-space: nowrap;
    font-weight: normal;
    border: 1px solid rgb(0, 0, 0);
    background-color: #EAC645;
}

.tui-pagination .tui-prev-is-ellip {
   //  border-right: 0
}

.tui-pagination .tui-next-is-ellip {
   //  border-left: 0
}

.tui-pagination .tui-first+.tui-prev, .tui-pagination .tui-next+.tui-last {
   //  border-left: 0
}

.tui-pagination .tui-ico-first, .tui-pagination .tui-ico-prev, .tui-pagination .tui-ico-next, .tui-pagination .tui-ico-last, .tui-pagination .tui-ico-ellip {
    background: url(data:image/png;base64,) no-repeat 0 0;
    display: inline-block;
    vertical-align: text-top;
    font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
   background-color: transparent;
    padding: 10px;
    line-height: 1;
}

.tui-pagination .tui-prev {
    margin-right: 14px;
}

.tui-pagination .tui-next {
    margin-left: 6px;
    margin-right: 5px;
}

.tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px
}

.tui-pagination .tui-ico-prev {
   fill: rgb(179, 175, 175);
    width: 6px;
    height: 10px;
    background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
    width: 6px;
    height: 10px;
    background-position: -13px -24px
}

.tui-pagination .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px
}

.tui-pagination .tui-ico-ellip {
    width: 10px;
    height: 2px;
    margin-top: 12px;
   //  vertical-align: bottom;
    background-position: 0px -24px
}

.tui-pagination .tui-is-disabled:hover {
   //  background:  #000;
   //  cursor: default
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
    background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
    background-position: -13px 0px
}

.tui-pagination .tui-is-disabled .tui-ico-next {
    background-position: -21px -24px
}

.tui-pagination .tui-is-disabled .tui-ico-last {
    background-position: -13px -12px
}

.tui-page-inputs {
    display: inline-block;
    vertical-align: center;
}

.tui-page-inputs .tui-page-input {
    display: inline-block;
    width: 10px;
    height: 12px;
    padding: 10px;
    font-size: 12px;
    text-align: center;
    color: #333;
    border: 0;
    outline: none;
}

.tui-page-inputs .tui-page-current {
    color: #4b96e6
}

.tui-page-separator {
    display: inline-block;
    font-size: 12px;
    height: 12px;
    padding: 8px 0 6px;
    vertical-align: top
}

@media screen and (min-width: 768px) {
    .tui-pagination .tui-page-btn {
    width: 44px;
    height: 44px;
    padding: 11px 0;
}

.tui-pagination .tui-first, .tui-pagination .tui-prev, .tui-pagination .tui-next, .tui-pagination .tui-last, .tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip{
       height: 44px;
       line-height: 26px;
}

.tui-pagination .tui-page-btn {
    margin-right: 10px;
}
.tui-pagination .tui-prev {
    margin-right: 24px;
}
.tui-pagination .tui-first{
    margin-right: 8px;
}
.tui-pagination .tui-next {
    margin-right: 8px; 
    margin-left: 14px;
}

.tui-pagination .tui-ico-first, .tui-pagination .tui-ico-prev, .tui-pagination .tui-ico-next, .tui-pagination .tui-ico-last, .tui-pagination .tui-ico-ellip {
    vertical-align: sub;
}
}

.tui-pagination .tui-is-disabled.tui-first{
    background-color: black;}

.tui-pagination .tui-is-disabled.tui-prev{
    background-color: black;
}
.tui-pagination .tui-is-disabled.tui-next{
    background-color: black;
}
.tui-pagination .tui-is-disabled.tui-last{
    background-color: black;
}

body.dark .tui-pagination .tui-page-btn {
    color: #fff;
    border: 1px solid #fff;
}

body.dark .tui-pagination .tui-page-btn:hover {
    color:  #EAC645;

}

body.dark .tui-pagination .tui-is-selected{
    color: black;
    background-color: #fff;
}
body.dark .tui-ico-ellip{
    color: #fff;
}

