@import "https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,500;1,400&display=swap";
/*! modern-normalize v1.1.0 | MIT License | https://github.com/sindresorhus/modern-normalize */
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button {
  -webkit-appearance: button;
}

[type="button"] {
  -webkit-appearance: button;
}

[type="reset"] {
  -webkit-appearance: button;
}

[type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --white-logo-color: #f3f3f3;
  --dark-logo-color: #000;
  --main-white-color: #fff;
  --main-dark-color: #111;
  --main-bg-color: #f6f6f6;
  --main-dark-bg-color: #202024;
  --accent-color: #4f2ee8;
  --another-accent-color: #eac645;
  --categories-color: rgba(17, 17, 17, .6);
  --paragraf-color: #b4afaf;
  --backdrop-bg-color: rgba(17, 17, 17, .4);
  --messege-color: rgba(0, 0, 0, .6);
  --congratulations-color: (rgba(17, 17, 17, .5));
}

body {
  background-color: var(--main-bg-color);
  color: var(--main-dark-color);
  font-family: DM Sans, sans-serif;
  font-style: normal;
}

.dark {
  background-color: var(--main-dark-bg-color);
  color: var(--main-white-color);
}

h1, h2, h3 {
  margin: 0;
  padding: 0;
}

p, ul, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  border-radius: 8px;
}

.btn {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
}

.lock-body {
  overflow: hidden;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-top: 40px;
}

@media screen and (max-width: 767px) {
  .container {
    width: 100%;
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    gap: 40px;
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
    display: flex;
  }
}

.container-home {
  margin-bottom: 80px;
}

.home-title {
  letter-spacing: -1.28px;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
}

.home-title-accent {
  color: var(--accent-color);
}

.home-category-title {
  color: var(--paragraf-color);
  letter-spacing: .42px;
  text-transform: uppercase;
  margin-bottom: 18px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.29;
}

.home-books-field {
  flex-direction: column;
  align-items: start;
  max-width: 335px;
  display: flex;
}

.home-books-field:not(:last-child) {
  margin-bottom: 40px;
}

.home-book-list {
  cursor: pointer;
  flex-direction: column;
  row-gap: 40px;
  max-width: 335px;
  margin-bottom: 8px;
  display: flex;
}

.home-book-item {
  width: 335px;
}

.home-thumb-photo {
  border-radius: 0 0 8px 8px;
  margin-bottom: 14px;
  position: relative;
  overflow: hidden;
}

.overlay-text {
  opacity: 0;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.home-book-photo {
  width: 100%;
  max-width: 335px;
  height: 485px;
  display: block;
}

.home-book-name {
  letter-spacing: -.64px;
  text-transform: uppercase;
  margin: 0 0 4px;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.13;
}

.home-book-author {
  color: var(--paragraf-color);
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  line-height: 1.17;
}

.load-more {
  border: 2px solid var(--accent-color);
  letter-spacing: -.14px;
  background: inherit;
  color: inherit;
  cursor: pointer;
  border-radius: 40px;
  align-self: flex-end;
  align-items: flex-start;
  padding: 14px 28px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.29;
}

.load-more:active {
  color: #fff;
  background: #4f2ee8;
}

.blur-up {
  filter: blur(5px);
  transition: filter .4s, -webkit-filter .4s;
}

.blur-up.lazyloaded {
  filter: blur();
}

@media screen and (min-width: 768px) {
  .container-home {
    margin-bottom: 100px;
  }

  .home-title {
    letter-spacing: -1.92px;
    font-size: 48px;
    line-height: 1.08;
  }

  .home-books-field {
    max-width: 704px;
  }

  .home-category-title {
    line-height: 1.7;
  }

  .home-book-list {
    flex-flow: wrap;
    column-gap: 25px;
    max-width: 704px;
    margin-bottom: 20px;
    display: flex;
  }

  .home-book-item {
    width: 218px;
  }

  .home-book-photo {
    max-width: 218px;
    height: 316px;
    display: block;
  }

  .home-book-author {
    margin-bottom: 8px;
  }
}

@media screen and (min-width: 1440px) {
  .container-home {
    margin-bottom: 72px;
  }

  .all-books-area, .home-books-field {
    max-width: 996px;
  }

  .home-book-list {
    column-gap: 24px;
    max-width: 996px;
    margin-bottom: 32px;
  }

  .home-book-item {
    width: 180px;
  }

  .overlay-text {
    opacity: 0;
    opacity: 0;
    letter-spacing: -.56px;
    text-transform: uppercase;
    color: var(--main-dark-color);
    background: var(--another-accent-color);
    border-radius: 0 0 8px 8px;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 70px;
    margin: 0;
    padding: 0 52px;
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translateY(100%);
  }

  .home-book-item:hover .overlay-text {
    opacity: 1;
    transform: translateY(0);
  }

  .home-book-photo {
    max-width: 180px;
    height: 256px;
    display: block;
  }

  .load-more:hover, .load-more:focus {
    color: #fff;
    background: #4f2ee8;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1439px) {
  .sidebar-container {
    gap: 32px;
    margin-bottom: 100px;
    display: flex;
  }
}

.header-btn {
  cursor: pointer;
}

body.dark .page-header {
  background-color: var(--main-dark-color);
  border: 1.5px solid var(--main-white-color);
}

body.dark .logo-text {
  color: var(--white-logo-color);
}

body.dark .nav-link {
  color: var(--main-white-color);
}

body.dark .icon-header {
  fill: var(--main-white-color);
}

body.dark .current {
  color: var(--main-dark-color);
}

body.dark .shoplist.current {
  padding-top: 7px;
  padding-bottom: 7px;
}

body.dark .current .icon-header {
  fill: var(--main-dark-color);
}

body.dark .icon-menu, body.dark .icon-cross {
  stroke: var(--main-white-color);
}

body.dark .log-out-btn, body.dark .sign-up-btn {
  color: var(--main-white-color);
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
}

.page-header {
  background-color: var(--main-white-color);
  border: 1.5px solid var(--main-dark-color);
  border-radius: 0 0 8px 8px;
  max-width: 1440px;
  margin: 0 auto;
}

.header-container {
  align-items: center;
  padding: 16px 20px;
  display: flex;
  position: relative;
}

.logo-container {
  align-items: center;
  gap: 8px;
  margin-right: auto;
  display: flex;
}

.logo-text {
  color: var(--dark-logo-color);
  text-align: center;
  width: 77px;
  font-size: 16px;
  font-weight: 600;
}

.nav-list {
  display: none;
}

.nav-link {
  letter-spacing: -.56px;
  text-transform: uppercase;
  color: var(--main-dark-color);
  transition: text-shadow var(--transition-dur-tfunc);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
}

.current {
  background-color: var(--another-accent-color);
  border-radius: 36px;
  padding: 8px 16px;
  font-weight: 700;
}

.shoplist {
  align-items: flex-end;
  gap: 4px;
  display: flex;
}

.logo-container:hover, .logo-container:focus {
  text-shadow: 0px 0px 1px var(--another-accent-color);
  transform: scale(1.02);
}

.icon-header {
  fill: var(--main-dark-color);
}

.right-header {
  align-items: center;
  gap: 14px;
  margin-left: auto;
  display: flex;
}

.switch {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(180deg, var(--accent-color) 0%, #dcdcdc 100%);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(8, 8, 8, .1);
}

.slider:before {
  content: "";
  background-color: #fff;
  width: 20px;
  height: 20px;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

input:checked + .slider, input:focus + .slider {
  background: linear-gradient(180deg, var(--accent-color) 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + .slider:before {
  transform: translateX(16px);
}

.slider.round {
  border-radius: 24px;
}

.slider.round:before {
  border-radius: 50%;
}

.menu-button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
}

.menu-button .icon-cross {
  display: none;
}

.menu-button.is-open .icon-cross {
  display: block;
}

.menu-button.is-open .icon-menu {
  display: none;
}

.icon-menu, .icon-cross {
  stroke: var(--main-dark-color);
}

.menu-button:hover .icon-menu, .menu-button:hover .icon-cross {
  stroke: var(--accent-color);
}

.auth-btn {
  color: var(--main-white-color);
  background-color: var(--accent-color);
  border: 1.5px solid var(--accent-color);
  letter-spacing: -.44px;
  border-radius: 36px;
  align-items: center;
  width: 184px;
  height: 46px;
  padding-left: 12px;
  padding-right: 12px;
  font-size: 22px;
  font-weight: 700;
  display: flex;
}

.auth-img {
  color: #fff;
  background-color: var(--main-white-color);
  border-radius: 50%;
  justify-content: space-around;
  align-items: center;
  width: 37px;
  height: 37px;
  margin-right: 9px;
  display: flex;
}

.auth-user-icon {
  fill: var(--accent-color);
}

.auth-icon {
  fill: var(--main-white-color);
  margin-left: 4px;
}

.unauth-btn {
  letter-spacing: -.56px;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  width: 184px;
  height: 46px;
  padding: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: flex;
}

.log-out-btn {
  color: var(--main-dark-color);
  background-color: var(--main-white-color);
  border: 1.5px solid var(--main-dark-color);
}

.arrow-right-icon {
  stroke: var(--another-accent-color);
}

.sign-up-btn {
  color: var(--main-white-color);
  background-color: var(--accent-color);
  border: 1.5px solid var(--main-dark-color);
}

.purpul-background {
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #6f53ef 18.03%, #fff 100%);
}

.overlay {
  z-index: 12;
  background-image: url("Menu.4ded615d.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: calc(100vh - 65.5px);
  padding: 24px 20px 20px;
  transition: transform .25s linear;
  position: fixed;
  top: 67.5px;
  left: 0;
  overflow: auto;
  transform: translateX(-100%);
}

.overlay.is-open {
  pointer-events: initial;
  visibility: visible;
  opacity: 1;
  height: calc(100vh - 65.5px);
  left: 50%;
  overflow: hidden;
  transform: translateX(-50%);
}

.container-mobile-menu {
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  display: flex;
}

.mobile-menu-user {
  color: var(--main-white-color);
  letter-spacing: -.44px;
  align-items: center;
  margin-bottom: 13vh;
  font-size: 22px;
  font-weight: 700;
  display: flex;
}

.user-icon {
  border-radius: 50%;
  width: 37px;
  height: 37px;
  margin-right: 9px;
}

.mobile-menu-wrapper {
  justify-content: center;
  display: flex;
}

.mobile-menu-list {
  flex-direction: column;
  display: flex;
}

.mobile-menu-item {
  text-align: center;
}

.mobile-menu-link {
  letter-spacing: -.56px;
