}

.link-home {
  background-color: var(--another-accent-color);
  color: var(--main-dark-color);
  border-radius: 36px;
  justify-content: center;
  width: 72px;
  margin-bottom: 16px;
  padding: 8px 16px;
  font-weight: 700;
  display: flex;
}

.link-shop {
  color: var(--main-white-color);
  justify-content: center;
  align-items: center;
  font-weight: 500;
  display: flex;
}

.link-shop-icon {
  fill: currentColor;
  margin-left: 4px;
}

.mobile-logout-button {
  border: 1.5px solid var(--main-dark-color);
  background-color: var(--main-white-color);
  color: var(--main-dark-color);
  letter-spacing: -.56px;
  border-radius: 18px;
  align-items: center;
  width: 120px;
  margin-top: auto;
  padding: 15px 24px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
