.scroll-up.show {
  opacity: 1;
}

.scroll-up {
  z-index: 30;
  opacity: 0;
  transition: opacity .6s linear;
  display: flex;
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.scroll-up.show {
  opacity: 1;
}

@media screen and (max-width: 767px) {
  .backdrop {
    z-index: 100;
    background-color: rgba(46, 46, 66, .4);
    justify-content: center;
    align-items: start;
    width: 100vw;
    height: 100vh;
    max-height: 100vh;
    transition: opasity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1), transform .5s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: scroll;
    transform: scale(1);
  }

  body.is-hidden .modal {
    transform: scale(.6);
  }

  body.is-hidden .backdrop {
    opacity: 0;
    height: 0;
    margin: 0;
    display: none;
  }

  body.dark .modal {
    background-color: var(--main-dark-color);
    border-color: var(--main-white-color);
  }

  .modal {
    border: 2px solid var(--main-dark-color);
    background-color: var(--main-bg-color);
    border-radius: 18px;
    flex-direction: column;
    max-width: 336px;
    max-height: 816px;
    margin: 80px 0;
    padding: 40px 24px;
    transition: opacity .5s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: relative;
  }

  .modal-text-container {
    margin: 0;
  }

  body.dark .modal-img {
    fill: var(--main-white-color);
  }

  .modal-close-btn {
    background-color: var(--main-bg-color);
    border: 2px solid var(--main-bg-color);
    cursor: pointer;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    padding: 0;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 12px;
    right: 12px;
  }

  body.dark .modal-close-btn {
    background-color: var(--main-dark-color);
    border-color: var(--main-dark-color);
  }

  body.dark .modal-close-btn:hover, .modal-close-btn:focus, .modal-close-btn:hover {
    border-color: var(--accent-color);
  }

  .modal-close-icon {
    stroke: var(--main-dark-color);
    width: 24px;
    height: 24px;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .modal-close-icon:hover, .modal-close-icon:focus {
    stroke: var(--accent-color);
  }

  body.dark .modal-close-icon {
    stroke: var(--main-white-color);
  }

  body.dark .modal-close-icon:hover, body.dark .modal-close-icon:focus {
    stroke: var(--accent-color);
  }

  .modal-sub-btn {
    letter-spacing: -.14px;
    text-align: center;
    color: var(--main-dark-color);
    border: 2px solid var(--accent-color);
    text-transform: uppercase;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0);
    border-radius: 40px;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 280px;
    margin: 0 auto 8px;
    padding: 14px 20px;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.28;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  }

  body.dark .modal-title {
    color: var(--main-white-color);
  }

  .modal-sub-btn:focus, .modal-sub-btn:hover {
    background: var(--accent-color);
    color: var(--main-white-color);
  }

  body.dark .modal-sub-btn {
    color: var(--main-white-color);
  }

  .modal-title {
    color: var(--main-dark-color);
    letter-spacing: -.64px;
    margin-bottom: 4px;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.12;
